@import "@libs/normalize";
@import "@common/fonts";
@import "@common/vars";
@import "@common/mixins";
@import "@modules/header";
@import "@modules/footer";

body {
	font-family: "Ubu-R";
}

.wrapper {
	min-height: 100%;
	display: grid;
	grid-template: minmax(90px, auto) 1fr minmax(566px, auto) / 1fr;
	grid-template-areas:
		"header"
		"main"
		"footer";
}
.header {
	grid-column: 1/2;
	grid-row: 1/2;
	display: grid;
	grid-template: 1fr/1fr minmax(auto, 1230px) 1fr;
	padding: 0 15px;
	z-index: 1;
	top: 0;
}
.main {
	grid-column: 1/2;
	grid-row: 2/3;
	display: grid;
	grid-template: 1fr/1fr minmax(auto, 1230px) 1fr;
	padding: 0 15px;
}
.footer {
	grid-column: 1/2;
	grid-row: 3/4;
	display: grid;
	grid-template: 1fr/1fr minmax(auto, 1230px) 1fr;
	padding: 0 15px;
	background-color: $titles;
}
